"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Bell, Menu, Plus } from "lucide-react"
import { useAuthStore } from "@/lib/auth-store"
import { ManagerSidebar } from "@/components/manager/manager-sidebar"
import { DashboardStats } from "@/components/manager/dashboard-stats"
import { AppointmentsList } from "@/components/manager/appointments-list"
import { PopularServices } from "@/components/manager/popular-services"
import { StaffPlanning } from "@/components/manager/staff-planning"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { EmployeePerformance } from "@/components/manager/employee-perfomance"
import { ClientsTab } from "@/components/manager/clients-tab"
import { ProductsTab } from "@/components/manager/products-tab"
import { AnalyticsTab } from "@/components/manager/analytics-tab"
import { Header } from "@/components/navigation/header"

export default function ManagerDashboard() {
  const router = useRouter()
  const { user, isAuthenticated } = useAuthStore()
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [activeTab, setActiveTab] = useState("dashboard")

  useEffect(() => {
    if (!isAuthenticated || (user?.role !== "gerant" && user?.role !== "admin")) {
      router.push("/auth/login")
    }
  }, [isAuthenticated, user, router])

  if (!isAuthenticated || (user?.role !== "gerant" && user?.role !== "admin")) {
    return null
  }

  return (
    <div className="flex h-screen bg-gray-50 dark:bg-gray-950">
      
      <ManagerSidebar
        isOpen={sidebarOpen}
        onClose={() => setSidebarOpen(false)}
        activeTab={activeTab}
        onTabChange={setActiveTab}
      />

        <Header />
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Header */}
        <header className="bg-white dark:bg-gray-900 border-b h-20 flex items-center justify-between px-6">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" className="lg:hidden" onClick={() => setSidebarOpen(true)}>
              <Menu className="w-5 h-5" />
            </Button>
            <h1 className="text-2xl font-bold">Tableau de bord</h1>
          </div>
          <div className="flex items-center gap-3">
            <Button variant="outline" className="bg-cyan-500 hover:bg-cyan-600 text-white border-cyan-500">
              <Plus className="w-4 h-4 mr-2" />
              Enregistrer un client
            </Button>
            <Button className="bg-cyan-500 hover:bg-cyan-600">
              <Plus className="w-4 h-4 mr-2" />
              Ajouter un rendez-vous
            </Button>
            <Button variant="ghost" size="icon">
              <Bell className="w-5 h-5" />
            </Button>
          </div>
        </header>

        {/* Main Content */}
        <main className="flex-1 overflow-y-auto p-6">
          <div className="max-w-7xl mx-auto space-y-6">
            {activeTab === "dashboard" && (
              <>
                <DashboardStats />

                <Tabs defaultValue="overview" className="w-full">
                  <TabsList>
                    <TabsTrigger value="overview">Vue d'ensemble</TabsTrigger>
                    <TabsTrigger value="appointments">Rendez-vous</TabsTrigger>
                    <TabsTrigger value="planning">Planning</TabsTrigger>
                  </TabsList>

                  <TabsContent value="overview" className="space-y-6 mt-6">
                    <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                      <div className="lg:col-span-2">
                        <EmployeePerformance />
                      </div>
                      <div>
                        <PopularServices />
                      </div>
                    </div>
                    <StaffPlanning />
                    <AppointmentsList />
                  </TabsContent>

                  <TabsContent value="appointments" className="mt-6">
                    <AppointmentsList />
                  </TabsContent>

                  <TabsContent value="planning" className="mt-6">
                    <StaffPlanning />
                  </TabsContent>
                </Tabs>
              </>
            )}

            {activeTab === "planning" && (
              <div className="space-y-6">
                <StaffPlanning />
                <AppointmentsList />
              </div>
            )}

            {activeTab === "clients" && <ClientsTab />}

            {activeTab === "products" && <ProductsTab />}

            {activeTab === "analytics" && <AnalyticsTab />}

            {activeTab === "settings" && (
              <div className="text-center py-12">
                <h2 className="text-2xl font-bold mb-2">Paramètres</h2>
                <p className="text-muted-foreground">Cette section sera bientôt disponible</p>
              </div>
            )}
          </div>
        </main>
      </div>
    </div>
  )
}
